:root {
    --green: #446644;
    --dark-green: #444544;
    --pale-green: #666766;
}
* {
    box-sizing: border-box;
    margin: 0;
    font-size: 12px;
    color: var(--dark-green);
}

body {
    margin: 32px;
    background: #bbbcbb;
    font-family: "inter", sans-serif;
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    width: 550px;
    border-radius: 16px;
    background: #eeeeee;
    box-shadow: 0 0 64px var(--dark-green);
    overflow: hidden;
}

nav {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 16px;
    background: var(--green);
    border-bottom: 1px solid var(--dark-green);
}

nav > img {
    width: 24px;
}

nav > p {
    margin-left: 8px;
    color: white;
    font-size: 14px;
}

.entry-list {
    display: flex;
    margin: 32px;
    flex-direction: column;
    gap: 20px;
}

.journal-entry {
    display: flex;
    align-items: flex-start;
    padding-top: 20px;
    border-top: 1px solid var(--pale-green);
}

.journal-entry:first-child {
    padding-top: 0;
    border-top: 0;
}

.sizzle-image {
    border-radius: 8px;
    border: 1px solid var(--pale-green);
    width: 125px;
    height: 168px;
}

.details-area {
    display: flex;
    flex-direction: column;
    padding: 16px;
    gap: 8px;
}

.country-bar {
    display: flex;
    align-items: baseline;
    gap: 4px;
    font-size: 16px;
}

.dropPin {
    width: 8px;
}

.country-title {
    font-size: 14px;
    letter-spacing: 0.075em;
}

.country-map {
    padding-left: 6px;
    color: var(--pale-green);
}

.location {
    font-size: x-large;
}

.description {
    color: var(--pale-green);
}